import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import pic11 from '../assets/images/pic11.jpg'

const Products = (props) => (
    <div>
        <Helmet>
            <title>Products</title>
            <meta name="description" content="Products" />
        </Helmet>

        <div id="main" className="alt">
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h1>Products</h1>
                    </header>
                    <span className="image main"><img src={pic11} alt="" /></span>
                        <p>For success in any online business, an user friendly website is a must as it will provide enhanced user experience to the online visitors. Any site that is too complex and difficult will definitely push away online traffic. The use of effective and simple user interface design will be of immense help in achieving the objectives of a website. A good user interface not only increases the site usability but also leads to the smooth completion of any task at hand thereby making everything enjoyable and flexible as per the requirements of users.</p>
                        <ul className="pagination">
                            <li><Link to="clean_design"className="button small">Prev</Link></li>
                            <li><Link to="performance" className= "page">1</Link></li>
                            <li><Link to="multi_device" className="page">2</Link></li>
                            <li><Link to="loading_speeds" className="page">3</Link></li>
                            <li><Link to="base_technologies" className="page">4</Link></li>
                            <li><Link to="clean_design" className="page">5</Link></li>
                            <li><Link to="#" className="page active">6</Link></li>
                            <li><span className="button small disabled">Next</span></li>
                        </ul>
                </div>
            </section>
        </div>

    </div>
)

export default Products


